.dashboard-cards {
display: grid;
grid-template-columns: repeat(3, 1fr);
grid-gap: 20px;
}

.dashboard-card {
background: #fff;
border: 1px solid #ddd;
padding: 20px;
box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
}

.table-dashboard {
width: 100%;
margin-top: 20px;
border-collapse: collapse;
}

.table-dashboard th, .table-dashboard td {
border: 1px solid #ddd;
padding: 8px;
text-align: left;
}

.table-dashboard th {
background-color: #f8f9fa;
}

